import React, { Component } from 'react'
import { Switch, Route } from 'react-router-dom'
import Home from './Body/Home.js'
import page from './Body/page/page.js'
import brand from './Body/page/brand.js'
import brandpage from './Body/page/brandpage.js';
import grocerypage from './Body/page/grocerypage.js';
import mallpage from './Body/page/mallpage.js';
import redmall from './Body/page/redmall.js';
import AboutUs from './Body/aboutus.js'
import Brands from './Body/Brands.js'
import Grocery from './Body/Grocery.js'
import Malls from './Body/Malls'
import RedMallGrid from './Body/MallpageGrid/RedMallPage'
import HaifaaMallGrid from './Body/MallpageGrid/HaifaMallPage'

export default class Main extends Component {
  render() {
    return (
      <div>
           <Switch>
      <Route exact path='./adv/' component={Home}/>
      <Route path='/aboutus' component={AboutUs} />
      <Route path='/brandpage/:number' component={brandpage} />
      <Route path='/grocerypage/:number' component={grocerypage} />
      <Route path='/mallpage/:number' component={mallpage} />
      <Route path='/redmall/:number' component={redmall} />
      <Route path='/Brands' component={Brands} />
      <Route path='/Grocery' component={Grocery} />
      <Route path='/Malls' component={Malls} />
      <Route path='/redseamall' component={RedMallGrid} />
      <Route path='/haifaamall' component={HaifaaMallGrid} />
    </Switch>
        
      </div>
    )
  }
}


