import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import titleBrandGrocery from './titleBrandGrocery';
import { Link } from 'react-router-dom'
import './GroceryGrid.css'
import withWidth, { isWidthUp } from '@material-ui/core/withWidth';

const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: theme.palette.background.paper,
    },
    gridList: {
      width: 1089,
      height: 330

    },
  });

  function GroceryGrid(props) {
    const { classes } = props;

    const getGridListCols = () => {
      if (isWidthUp('xl', props.width)) {
        return 4;
      }
  
      if (isWidthUp('lg', props.width)) {
        return 3;
      }
  
      if (isWidthUp('md', props.width)) {
        return 2;
      }
      if (isWidthUp('sm', props.width)) {
        return 2;
      }
      if (isWidthUp('xs', props.width)) {
        return 1;
      }
  
      return 1;
    }
  
    return (
      <div className={classes.root}>
      <h2 style={{textAlign: "center"}}>Grocery Trends with just one click!</h2>
        <GridList cellHeight={160} cols={getGridListCols} className="gorcerygrid">
          {titleBrandGrocery.map(tile => (
            <GridListTile key={tile.img} cols={tile.cols || 1}>
             <Link to={`/grocerypage/${tile.title}`}> <img src={tile.img} alt={tile.title} className="imagedropshadowgrocery" /></Link>
            </GridListTile>
          ))}
        </GridList>
      </div>
    );
  }

  GroceryGrid.propTypes = {
    classes: PropTypes.object.isRequired,
  };

  export default withStyles(styles)(GroceryGrid);
