import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import titleRedMallData from './titleredMallData';
import Grid from '@material-ui/core/Grid';
import { Link } from 'react-router-dom'
import './BrandGrid.css'
import withWidth, { isWidthUp } from '@material-ui/core/withWidth';
import Ads1 from '../ads/ads1'
import Ads2 from '../ads/ads2'

const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: theme.palette.background.paper,
    },
    gridList: {
      width: 1089,
      height: 330,
     

    },
  });

  function RedMallGrid(props) {
    const { classes } = props;
    const getGridListCols = () => {
      console.log("function")
      if (isWidthUp('xl', props.width)) {
        console.log('xl ')
        return 4;
      }
  
      if (isWidthUp('lg', props.width)) {
        console.log('lg ')
        return 3;
      }
  
      if (isWidthUp('md', props.width)) {
        console.log('md ')
        return 2;
      }
      if (isWidthUp('sm', props.width)) {
        return 2;
      }
      if (isWidthUp('xs', props.width)) {
        return 1;
      }
  
      return 1;
    }
    
  
    return (
      <div className={classes.root}>
        <Grid item lg={6} xs={12}><Ads1 /></Grid>
         <Grid item lg={6} xs={12}><Ads2 /></Grid>
         <br />
      <h2 style={{textAlign: "center"}}>Red Sea Mall Trends with just one click!</h2>
        <GridList cellHeight={160} cols={getGridListCols} className="brandgrid">
          {titleRedMallData.map(tile => (
            <GridListTile key={tile.img} cols={tile.cols || 1}>
             <Link to={`/redmall/${tile.title}`}> <img src={tile.img} alt={tile.title}  className="imagedropshadowbrand" /></Link>
            </GridListTile>
          ))}
        </GridList>
        <Grid item lg={6} xs={12}><Ads1 /></Grid>
         <Grid item lg={6} xs={12}><Ads2 /></Grid>
         <br />
      </div>
    );
  }

  RedMallGrid.propTypes = {
    classes: PropTypes.object.isRequired,
  };

  export default withStyles(styles)(RedMallGrid);
