import React, { Component } from 'react'
import './header.css'
import Clock from 'react-live-clock';
import { Link } from 'react-router-dom'
import hamburgerMenuPage from './Nav.js'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
});

export default class Header extends Component {
  render() {
    return (
      <div style={{height: 137,paddingBottom: 8}}>
        <div className="header" style={{  width: '100%'}}>
        <Grid container spacing={24}>
        <Grid item xs={4} lg={4}>
        <img src="/adv/assets/abc-logo.png" style={{width:400,padding: "4% 2% 2% 7%",
    paddingLeft: "7%"}}></img>
        </Grid>
        <Grid item xs={8} lg={8}>
        <Grid item xs={12} style={{paddingTop: 20}}>
        <img src="/adv/assets/tagline.png" style={{paddingLeft:"7%"}}></img>
        </Grid>
        <Grid item xs={12} style={{paddingTop: 20}}>
        <div id="nav">
    <ul>
        <li><Link to="/">HOME</Link></li>
        <li><Link to="/aboutus">ABOUT US</Link></li>
        <li><Link to="/Brands">BRANDS</Link></li>
        <li><Link to="/Grocery">GROCERY</Link></li>
        
        <li><Link to="/Malls">MALLS</Link></li>
        <li><Link to="/page">CONTACT US</Link></li>
      
    </ul>
</div>  
        </Grid>
        </Grid></Grid>
        {/* <div style={{float: "right", paddingRight: "13%"}}>
            <h3 style={{color: "white"}}>Saudia Arabia  </h3>
            <span><Clock style={{fontSize: '1.5em', color: "white"}}  format={'dddd, MMMM Mo, YYYY, h:mm:ss A'} timezone={'Etc/GMT-3'} ticking={true} /></span>
            
        </div> */}
            {/* <div style={{height: 96}}>
                <img src="/assets/demo-logo.png" style={{width:262}}></img>
            </div> */}
        </div>
        {/* <hamburgerMenuPage /> */}
      
      </div>
    )
  }
}
